// MODULES //

import config from './config.js';


// MAIN //

/**
* Application routes.
*
* @private
* @constant
* @type {Object}
*/
const routes = {
	'VERSION_DEFAULT': config.mount+':version',

	'SEARCH': config.mount+':version/search',
	'HELP': config.mount+':version/help',
	'NONPACKAGE_DEFAULT': config.mount+':version/*',

	'PACKAGE_DEFAULT': config.mount+':version/@stdlib/:pkg+',
	'PACKAGE_INDEX': config.mount+':version/@stdlib/:pkg+/index.html',
	'PACKAGE_BENCHMARKS': config.mount+':version/@stdlib/:pkg+/benchmarks',
	'PACKAGE_TESTS': config.mount+':version/@stdlib/:pkg+/tests',

	'ERROR_DECODER': config.mount+':version/error/decoder'
};


// EXPORTS //

export default routes;
