// MAIN //

/**
* Resets the window scroll position to the top of the page.
*
* @private
*/
function resetScroll() {
	if ( typeof window !== 'undefined' ) {
		window.scrollTo( 0, 0 );
	}
}


// EXPORTS //

export default resetScroll;
