// MAIN //

/**
* Removes a `@stdlib/` prefix from a package name.
*
* @private
* @param {string} pkg - package name
* @returns {string} package name with the `@stdlib/` path prefix removed
*
* @example
* var pkg = deprefix( '@stdlib/foo/bar' );
* // returns 'foo/bar'
*/
function deprefix( pkg ) {
	return pkg.replace( /^@stdlib\//, '' );
}


// EXPORTS //

export default deprefix;
